
.text-input{
  border-top:1px solid #ddd;
  padding: 10px;
  text-align: right;
  height:90px;
}
.text-input textarea{
  border:none;
  resize:none;
  width: 580px;
}
.text-input .btn{
  padding:6px 12px;
  border: 1px solid #fa5943;
  background: #fa5943;
  vertical-align: middle;
  text-decoration: none;
  color:#fff;
}



