
ul.list li{
  padding: 10px 14px;
  height: 50px;
}
ul.list li p{
  padding:4px 0 0 14px;
  text-overflow: ellipsis;
  white-space:nowrap;
  overflow:hidden; 
  font-size:12px;
  color:#666;
}
ul.list li p.name{
  font-size:14px;
  color: #333;
}
ul.list li img{
  width: 50px;
  height: 50px;
  border-radius:50%;
  float: left;
}
ul.list li:hover,ul.list li.active{
  background:#eee;
}



